import React from "react";
import { useNavigate } from "react-router-dom";
import { getRoundDescription } from "../util/getRoundDescription";

const ScoreButtonRow = ({ selectedCells, nextPlayerData, teamCount, params, isSaveFinal, maxCount, totalScore }) => {
  const { api, to_cd, kind_cd, detail_class_cd, rh_cd, refree, category } = params;
  const navigate = useNavigate();

  const handleMaxCheck = (nextLink) => {
    if (detail_class_cd % 2 === 0 && (teamCount === 0 || isNaN(teamCount))) {
      window.confirm("  .");
      return;
    }
    if (selectedCells !== maxCount) {
      window.confirm("   .");
      return;
    }

    if (nextLink) {
      navigate(nextLink);
    }
  };

  return (
    <table>
      <tbody>
        <tr>
          <td style={{ fontSize: "30px" }}> {totalScore} / 100</td>
        </tr>

        <tr className="button-row">
          <td colSpan={2} className="button-container1">
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                width: "100%",
              }}>
              {/* :   */}
              <span
                style={{
                  flex: "1",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  fontSize: "30px",
                }}>
                  ({selectedCells}/{maxCount})
              </span>

              {/* :  */}
              {nextPlayerData.index !== null &&
              (rh_cd === nextPlayerData.rh_cd || //   
                nextPlayerData.rh_cd !== "899000") ? ( //  1 2  (899000 )
                <button
                  onClick={handleMaxCheck.bind(null, nextPlayerData.link)}
                  className="nav-button"
                  style={{
                    flex: "1",
                    textAlign: "center",
                    backgroundColor: isSaveFinal === true ? "green" : "gray",
                    color: "white",
                  }}>
                  <div> {parseInt(detail_class_cd) % 2 === 0 ? "" : ""}</div>
                  {nextPlayerData.name} ({getRoundDescription(nextPlayerData.rh_cd)})
                </button>
              ) : (
                <button
                  className="nav-button"
                  style={{
                    flex: "1",
                    textAlign: "center",
                    backgroundColor: isSaveFinal === true ? "green" : "gray",
                    color: "white",
                  }}>
                  <div> .</div>
                  .
                </button>
              )}
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  );
};

export default ScoreButtonRow;
