// Refree.js
import React, { useState } from "react";
import { useParams } from "react-router-dom";
import "./Login.css";
import { encryptText } from "./util/encryptionUtils"; //   
import { useSetRecoilState } from "recoil";
import { refreeNameState } from "./util/recoild";
import { useNavigate } from "react-router-dom";

const Refree = () => {
  const { api, to_cd } = useParams(); //      pc   pk  url  .
  const [phoneNumber, setPhoneNumber] = useState(""); // input  phoneNumber state 
  const setRefreeName = useSetRecoilState(refreeNameState); //  recoil   ]
  const navigate = useNavigate();

  const handleLoginClick = () => {
    //   db 
    setRefreeName(""); //  
    const nextLink = `/${api}/${to_cd}/1`; // 
    navigate(nextLink);
  };

  const handlePhoneNumberChange = (e) => {
    // input     phoeNumber 
    const formattedPhoneNumber = e.target.value.replace(/\D/g, ""); //  
    setPhoneNumber(formattedPhoneNumber);
  };

  return (
    <>
      <div className="login-container">
        <h2> </h2>
        <label>
          <input type="tel" value={phoneNumber} onChange={handlePhoneNumberChange} onKeyDown={(e) => e.key === "Enter" && handleLoginClick()} placeholder="( )" />
        </label>
        <button type="button" onClick={handleLoginClick}>
          
        </button>
      </div>
    </>
  );
};

export default Refree;
